window.dom = {
  create(string){
    //Áî®trmolateÂÖÉÁ¥†‰Ωú‰∏∫ÂÆπÂô®
    const container = document.createElement("template");
    //trimÂéªÈô§Á©∫Ê†º
    container.innerHTML = string.trim();
    //content.firstChildÊâçËÉΩËé∑ÂèñÁ¨¨‰∏Ä‰∏™Â≠êÂÖÉÁ¥†
    return container.content.firstChild;
  },
  //ÂàõÂª∫ÊèíÂÖ•‰∏ã‰∏Ä‰∏™ËäÇÁÇπAPI
  after(node, node2){
    //ÊèíÂÖ•node‰∏ã‰∏Ä‰∏™ËäÇÁÇπÁöÑÂâçÈù¢
    node.parenNode.insertBefore(node2, node.nextSibling);
  },
  before(node, node2){
    node.parentNode.inserBefor(node2, node);
  },
  append(parent, node){
    parent.appendChild(node);
  },
  //Â¢ûÂä†‰∏Ä‰∏™Áà∂ÂÖÉÁ¥†
  wrap(node, parent){
    dom.before(node, parent);
    dom.append(parent, node);
  },
  remove(node){
    noed.parentNode.removeChild(node);
    return node;
  },
  //Âà†Èô§ÊâÄÊúâ
  empty(node){
    const {childNodes} = node;
    const array = [];
    let x = node.firstChild;
    while (x){
      array.push(dom.remove(node.firstChild));
      x = node.firstChild;
    }
  },
  attr(node, name, value){
    if(arguments === 3){
      node.setAttribute(name, value)
    }else if(arguments === 2){
      return node.getAttribute(name)
    }
  }
};




